/* === GLOBAL === */
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f7fa;
  /* gris très clair */
  color: #333;
}

.App {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
}

/* === HEADER / NOM & TITRE === */
header {
  background: linear-gradient(90deg, #6a11cb, #2575fc);
  /* violet -> bleu */
  color: #fff;
  text-align: center;
  padding: 2rem 1rem;
  border-radius: 0 0 15px 15px;
  margin-bottom: 2rem;
  position: relative;
}

header h1 {
  margin: 0;
  font-size: 2.5rem;
}

header h2 {
  margin: 0.3rem 0 0;
  font-weight: 400;
}

/* === SECTIONS === */
section {
  padding: 1.5rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  position: relative;
}

/* Couleurs harmonisées par section */
.about {
  background-color: #ffe0f0;
  /* rose clair */
}

.experience {
  background-color: #fff8dc;
  /* beige clair */
}

.skills {
  background-color: #e0ffe0;
  /* vert clair */
}

.projets-section {
  background-color: #f0f0ff;
  /* bleu très clair */
}

.contact {
  background-color: #fff0f5;
  /* lilas clair */
}

.rs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.skills-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

/* === TITRES DE SECTION === */
h3 {
  color: #333;
  border-bottom: 2px solid #ddd;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  text-align: center;
}

/* Titres spécifiques pour sections colorées */
.about h3 {
  color: #ff4d94;
}

/* rose vif */
.experience h3 {
  color: #ff9900;
}

/* orange */
.skills h3 {
  color: #28a745;
}

/* vert */
.projets-section h3 {
  color: #007bff;
}

/* bleu */
.contact h3 {
  color: #a64ca6;
}

/* violet/lilas */

/* === LIENS === */
a {
  color: #007bff;
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  color: #ff6600;
  text-decoration: underline;
}

/* === EXPERIENCE / JOBS === */
.job {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 8px;
  transition: transform 0.3s, background 0.3s;
}

.job:hover {
  transform: scale(1.02);
  background-color: #f5f5f5;
}

/* === COMPETENCES / SKILLS === */
.skill {
  display: inline-block;
  margin: 0.3rem;
  padding: 0.5rem 1rem;
  background-color: #007bff;
  color: #fff;
  border-radius: 20px;
  font-size: 0.9rem;
  transition: transform 0.3s, background 0.3s;
}

.skill:hover {
  transform: translateY(-2px);
  background-color: #0056b3;
}

/* === PROJETS === */
.projets {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  justify-content: center;
}

.projet-link {
  display: inline-block;
  padding: 0.6rem 1rem;
  background: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  font-weight: 500;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.5s forwards;
  transition: transform 0.3s, background 0.3s;
}

.projet-link:hover {
  transform: translateY(-3px);
  background-color: #ff6600;
}

/* Animation des projets */
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* === CONTACT / SOCIAL === */
.contact a {
  display: inline-block;
  margin: 0.5rem;
  padding: 0.6rem 1rem;
  background-color: #a64ca6;
  /* violet/lilas */
  color: #fff;
  border-radius: 5px;
  transition: all 0.3s;
}

.contact a:hover {
  background-color: #872b87;
  transform: translateY(-2px);
}



.formations {
  background-color: #fdf0e0;
  /* fond orange clair */
  padding: 1.5rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.formations h3 {
  color: #ff6600;
  /* titre orange */
  text-align: center;
  margin-bottom: 1rem;
}

.formation {
  margin-bottom: 1.5rem;
}

.formation h4 {
  color: #ff8c42;
  /* titre plus clair pour chaque formation */
  margin-bottom: 0.3rem;
}

.formation p {
  font-style: italic;
  color: #555;
  margin-bottom: 0.5rem;
}

.formation ul {
  padding-left: 1.2rem;
  margin: 0;
}

.formation li {
  margin-bottom: 0.3rem;
}

.diplomes {
  background-color: #e0f7ff;
  /* bleu très clair */
  padding: 1.5rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.diplomes h3 {
  color: #0099cc;
  /* titre bleu */
  text-align: center;
  margin-bottom: 1rem;
}

.diplome {
  margin-bottom: 1.5rem;
}

.diplome h4 {
  color: #00aaff;
  /* titre diplôme */
  margin-bottom: 0.3rem;
}

.diplome p {
  font-style: italic;
  color: #555;
  margin-bottom: 0.5rem;
}

.diplome ul {
  padding-left: 1.2rem;
  margin: 0;
}

.diplome li {
  margin-bottom: 0.3rem;
}


/* === RESPONSIVE === */
@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }

  .projets {
    flex-direction: column;
    align-items: center;
  }

  .skill {
    margin: 0.2rem;
  }
}

/* === PARTICLES BACKGROUND === */
.particles-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* pour que le contenu reste au-dessus */
}
